.export-container {
  position: relative;
  height: 340px;
}
.export-container .search-wrapper {
    padding: 0 18px 0 20px;
}
.export-container .business-selector {
    position: absolute;
    top: 20px;
    width: 255px;
    height: 32px;
}
.export-container .template-search {
    position: relative;
    margin-left: 265px;
    margin-bottom: 20px;
}
.export-container .template-search .search-input {
      padding: 0 40px 0 10px;
      width: 255px;
      height: 32px;
      line-height: 32px;
      font-size: 14px;
      background: #ffffff;
      border: 1px solid #c3cdd7;
      border-radius: 4px;
      outline: none;
}
.export-container .template-search .search-input:hover {
        border-color: #c0c4cc;
}
.export-container .template-search .search-input:focus {
        border-color: #3c96ff;
}
.export-container .template-search .search-input:focus + i {
          color: #3c96ff;
}
.export-container .template-search .common-icon-search {
      position: absolute;
      right: 15px;
      top: 9px;
      color: #dddddd;
}
.export-container .template-search .bk-selector {
      width: 255px;
}
.export-container .template-wrapper {
    float: left;
    padding: 20px 4px 20px 0;
    width: 557px;
    height: 100%;
}
.export-container .template-wrapper .template-list {
      padding: 0 14px 0 20px;
      height: 268px;
      overflow-y: auto;
}
.export-container .template-wrapper .template-list::-webkit-scrollbar {
        width: 4px;
        height: 4px;
}
.export-container .template-wrapper .template-list::-webkit-scrollbar-thumb {
          border-radius: 20px;
          background: #a5a5a5;
          -webkit-box-shadow: inset 0 0 6px rgba(204, 204, 204, .3);
                  box-shadow: inset 0 0 6px rgba(204, 204, 204, .3);
}
.export-container .template-wrapper .template-group {
      margin-bottom: 30px;
}
.export-container .template-wrapper .search-list {
      padding-top: 40px;
}
.export-container .template-wrapper .group-name {
      margin-bottom: 8px;
      font-size: 12px;
}
.export-container .template-item {
    display: inline-block;
    margin: 0 0 7px 10px;
    width: 252px;
    background: #dcdee5;
    border-radius: 2px;
    cursor: pointer;
}
.export-container .template-item:nth-child(2n + 1) {
      margin-left: 0;
}
.export-container .template-item .template-item-icon {
      float: left;
      width: 56px;
      height: 56px;
      line-height: 56px;
      background: #c4c6cc;
      font-size: 24px;
      color: #ffffff;
      text-align: center;
}
.export-container .template-item .template-item-name {
      margin-left: 56px;
      padding: 0 12px;
      height: 56px;
      line-height: 56px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      color: #313238;
}
.export-container .template-item:nth-child(2n) {
      margin-right: 0;
}
.export-container .template-item-selected .template-item-icon {
    background: #666a7c;
}
.export-container .template-item-selected .template-item-name {
    background: #838799;
    color: #ffffff;
}
.export-container .empty-template {
    padding-top: 40px;
}
.export-container .selected-wrapper {
    width: 292px;
    height: 100%;
    margin-left: 557px;
    padding-right: 4px;
    border-left: 1px solid #dde4eb;
}
.export-container .selected-wrapper .selected-area-title {
      padding: 28px 20px 22px;
      line-height: 1;
      font-size: 14px;
      font-weight: 600;
      color: #313238;
}
.export-container .selected-wrapper .selected-area-title .select-count {
        color: #3a84ff;
}
.export-container .selected-list {
    padding-top: 8px;
    height: 276px;
    overflow-y: auto;
}
.export-container .selected-list::-webkit-scrollbar {
      width: 4px;
      height: 4px;
}
.export-container .selected-list::-webkit-scrollbar-thumb {
        border-radius: 20px;
        background: #a5a5a5;
        -webkit-box-shadow: inset 0 0 6px rgba(204, 204, 204, .3);
                box-shadow: inset 0 0 6px rgba(204, 204, 204, .3);
}
.export-container .selected-list .selected-item {
      position: relative;
      margin: 0 0 10px 14px;
      width: 254px;
      height: 56px;
      border-radius: 2px;
}
.export-container .selected-list .selected-item:hover .selected-delete {
        display: inline-block;
}
.export-container .selected-list .selected-item-icon {
      float: left;
      width: 56px;
      height: 56px;
      line-height: 56px;
      background: #666a7c;
}
.export-container .selected-list .selected-item-icon .selected-name {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        font-size: 24px;
        color: #ffffff;
}
.export-container .selected-list .selected-item-name {
      margin-left: 56px;
      padding: 0 12px;
      height: 56px;
      line-height: 56px;
      background: #838799;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      color: #ffffff;
}
.export-container .selected-list .selected-delete {
      display: none;
      position: absolute;
      top: -7px;
      right: -7px;
      padding: 2px;
      color: #cecece;
      background: #ffffff;
      border-radius: 50%;
      cursor: pointer;
}
.export-container .template-checkbox {
    position: absolute;
    left: 20px;
    bottom: -42px;
    cursor: pointer;
}
.export-container .template-checkbox .checkbox {
      display: inline-block;
      position: relative;
      width: 14px;
      height: 14px;
      color: #ffffff;
      border: 1px solid #c3cdd7;
      border-radius: 2px;
      text-align: center;
      vertical-align: -2px;
}
.export-container .template-checkbox .checkbox:hover {
        border-color: #999999;
}
.export-container .template-checkbox .checkbox.checked {
        background: #3c96ff;
        border-color: #3c96ff;
}
.export-container .template-checkbox .checkbox.checked::after {
          content: "";
          position: absolute;
          left: 2px;
          top: 2px;
          height: 4px;
          width: 8px;
          border-left: 1px solid;
          border-bottom: 1px solid;
          border-color: #ffffff;
          -webkit-transform: rotate(-45deg);
                  transform: rotate(-45deg);
}
.export-container .template-checkbox .checkbox-disabled {
      display: inline-block;
      position: relative;
      width: 14px;
      height: 14px;
      color: #cfcccc;
      cursor: not-allowed;
      border: 1px solid #c3cdd7;
      border-radius: 2px;
      text-align: center;
      vertical-align: -2px;
}
.export-container .template-checkbox .checkbox-disabled::after {
        background: #545454;
}
